import React, { Component } from 'react';
import L from "leaflet";
import "./CanvasWithExtraStyles";
import * as polygonGeoJson from "./polygon";
import "leaflet/dist/leaflet.css";
import Modal from "./ModalView";
import Camera from "./Camera";
import AppBar from "./AppBar";

const getBase64 = (file) => {
  return new Promise((resolve,reject) => {
     const reader = new FileReader();
     reader.onload = () => resolve(reader.result);
     reader.onerror = error => reject(error);
     reader.readAsDataURL(file);
  });
}

class Map extends Component {

	// Camera Object funcs
	// <input 
	// type="file" 
	// id="imageFile" 
	// name='imageFile' 
	// onChange={this.imageUpload} />
	// imageUpload = (e) => {
	//  const file = e.target.files[0];
	//  getBase64(file).then(base64 => {
	//    localStorage["fileBase64"] = base64;
	//    console.debug("file stored",base64);
	//  });
	// };

	componentDidMount() {
		const coords = [54.483407850984655, -6.104691472848336];
		// get data
		fetch(`https://api.openweathermap.org/data/2.5/weather?lat=${coords[0]}&lon=${coords[1]}&appid=4c20a7355e7d6a3957ef39dbd37bcd62`)
		  .then(response => response.json())
		  .then(data => console.log(data));

		const mapView = L.map("mapView", {
			center: coords,
			zoom: 15,
			renderer: new L.Canvas.WithExtraStyles()
		});

	new L.tileLayer(
		'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
		{
			attribution: `attribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>`,
			detectRetina: true,
			maxZoom: 30
		}
	).addTo(mapView);

	const polyStyle = {
		fillColor: "transparent",
		color: "#2f528f",
		extraStyles: [
			{
				inset: 0.9,
				color: "#d9d9d9",
				weight: 10,
				fillColor: "cyan",
				fillOpacity: 0.1
			},
			{
				inset: 1.1,
				color: "darkgray",
				weight: 10,
				fillColor: undefined
			},
			{
				inset: 1.2,
				color: "navy",
				opacity: 0.1,
				weight: 10,
				fillColor: undefined
			}
		]
	};
	
	const pointStyle = {
		radius: 4,
    fillColor: "#ff7800",
    color: "#000",
    weight: 1,
    opacity: 1,
    fillOpacity: 0.8
	};

	const whenClicked = (e) => {
		console.log(e);
	}

	const onEachFeature = (feature, layer) => {
		//bind click
		layer.on({
			click: whenClicked
		});
	}

	L.geoJson(polygonGeoJson, {
		style: function(feature) {
			const type = feature.geometry.type;
			switch (type) {
				case "Point":
					// return pointStyle;
					break;
				case "Polygon":
					return polyStyle;
					break;
			}
		},
		pointToLayer: function (feature, latlng) {
			const type = feature.geometry.type;
			switch (type) {
				case "Point":
				console.log('kloop')
				return L.circleMarker(latlng, pointStyle);
				break;
			}
        
    },
		onEachFeature: onEachFeature
	}).addTo(mapView);
}

	render() {
		return (
			<div style={{ position: 'relative' }}>
				<AppBar />
				<div id="mapView" style={{ position: 'relative', bottom: 8, height: '95vh', width: '95vw', border: '1px solid red'}}>
				</div>
			</div>
		)
	}
}

export default Map;




